html {
  --blue-color: #00acdc;
  --header-height: 60px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  margin: 0;
}

.container {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo img {
  width: 40px;
  height: 40px;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  display: flex;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__link {
  height: var(--header-height);
  line-height: var(--header-height);
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover {
  color: var(--blue-color);
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: var(--blue-color);
  font-weight: 400;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
